@import 'reset';
$background-color: rgba(48, 62, 77, 1.0);
$light-tan: #F4EFE7;
$light-mustard: #DEAB2B;
$tan: #ECE5D6;
$mustard: #C2940C;
$james-peach: #D99441;
$light-blue: #326173;
$navy: #0B1C26;



#map {
  height: 780px;
  width: 85%;
}
/* Optional: Makes the sample page fill the window. */
html, body {
  height: 100%;
  margin: 0;
  padding: 0px;
  background-color: $navy;
}

#Title-beer {
  font-size: 24px;
  font-weight: bold;
  color: white;
  padding: 20px 30px 10px 30px;
  font-family: 'Nunito', sans-serif;
  line-height: 1em;
}

#control{
		float: left;
		color: black;
		width: 15%;
    height: 780px;
    background-color: $light-blue;
	}

  #control h2, p {
    padding: 20px 30px 10px 30px;
    color: white;
    font-family: 'Open Sans', sans-serif;
    line-height: 1.3em;
  }

	#control input[type=text]{
		width: 68%;
		font-size: 16px;
		background-color: rgba(240, 234, 235, 1.0);
		border: 1px solid black;
    margin-left: 30px;
    margin-top: 10px;
	}

  #control button {
    margin-left: 30px;
      margin-top: 10px;
  }

.runButton  {
  margin-left: 30px;
  margin-top: 10px;
}

  .example {
    width: 100%;
    height: 800px;
    background-image: url("images/beer-line.jpg");
    background-size: cover;
  }

  .example-2 {
    width: 100;
  }

  .example-2 p {
    // width: 96%;
    height: 300px;
    background-color:  $light-blue;
    font-weight: bold;
    font-size: 28px;
    text-align: center;
    padding: 10%;
    // padding-top: 13%;
    // padding-bottom: 13%;
    line-height: 1.5em;
    color: white;
    font-family: 'Open Sans', sans-serif;
  }

  .example-2 p span {
    line-height: 1.5em;
    font-size: 16px;
    color: white;
    text-align: center;
    padding: 10px;
  }

.theList {
  padding: 60px;
}

.theList li {
  color: white;
  background-color: white;
  padding: 5px;
  display: inline-block;
  border: 2px solid $light-blue;
  border-radius: 10px;
  margin: 10px;
  font-family: 'Open Sans', sans-serif;
}

header {
  height: 60px;
  width: 100%;
  background-color: $navy;
  // z-index: 1;
}

.nav-tabs {
  padding: 5px;
  font-size: 16px;
  padding-top: 5px;
  // border-bottom: 1px solid white;
  // border-left: 1px solid white;
  // border-right: 1px solid white;
  border-radius: 0 0 5px 5px;
  display: inline-block;
  float: left;
  height: 25%;
  width: 60px;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  margin-bottom: 5px;
  margin-left: 10px;
  color: black;
  background-color: $light-blue;
  -webkit-transition: width 2s; /* For Safari 3.1 to 6.0 */
  transition: width 2s;
}

.nav-tabs-on {
  height: 35%;
}

.nav-tabs:hover {
  height: 35%;
}

.nav-2,
.nav-2-on {
  cursor: pointer;
  border-bottom: 1px solid $light-blue;
  color: $navy;
  padding: 5px;
  font-size: 16px;
  padding-top: 5px;
  display: inline-block;
  font-family: 'Open Sans', sans-serif;
  float: left;
  height: 20%;
  width: 90px;
  text-align: center;
  margin-top: 20px;
  margin-left: 10px;
  -webkit-transition: width 2s; /* For Safari 3.1 to 6.0 */
  transition: width 2s;
}

.nav-2-on {
  background-color: $light-blue;
  color: white;
  border-radius: 5px 5px 0 0 ;
}

.nav-2:hover {
  background-color: $light-blue;
  color: white;
  border-radius: 5px 5px 0 0 ;
}

header a:link,
header a:visited  {
    text-decoration: none;
    color: white;
}

.learnButton a:link,
.learnButton a:visited {
  text-decoration: none;
  color: white;
}

.history-bar {
  float: left;
  color: black;
  width: 15%;
  height: 780px;
  background-color:  $light-blue;
}

